html{
    font-size: 10px;
}

@media only screen and (max-width:500px){
    html{
        font-size: 9px;
    }
}

@media only screen and (max-width:420px){
    html{
        font-size: 7.5px;
    }
}

@media only screen and (max-width:300px){
    html{
        font-size: 7px;
    }
}